* {
    padding: 0;
    margin: 0;
}


.tabl {
    border-style: solid;
    border-width: 4px;
    border-color: darkorange;
    width: 20em;
    height: max-content;
    text-align: center;
    font-style: italic;
    font-size: xx-large;
    border-radius: 15px;
    margin: 0.1em;
   

}
img {
    height: 100%;
    width: 100%;
    
}
body{
    background-color: darkolivegreen;
}
.wrapper{
    display: flex;
    flex-direction: row;

}
.cartitem{
    display:flex;
    flex-wrap: nowrap;
    
    justify-content: space-around;
}
.cart{
    display: flex;
    flex-direction: column;
    text-align: center;
    border: 5px solid darkorchid;
    width: 20em;
    border-radius: 15px;
}
.finish{
    display: flex;
    flex-direction: column;
    text-align: center;
    border: 10px solid darkturquoise;
    border-radius: 15px;
    width: 20em;
}
.form{
    display: flex;
    flex-direction: column;
    text-align: center;
    border: 10px solid darkred;
    border-radius: 15px;
    width: 30em;
}
